.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.loader {
  color: #3b2d5a;
  text-align: center;
  width: 700px;
  height: 100px;
  flex-direction: column;
  align-items: center;
  margin-top: 50px;
  margin-left: 100px;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  display: block;
  position: absolute;
}

.loader:before {
  content: "";
  width: 15px;
  height: 15px;
  color: #3b2d5a;
  box-sizing: border-box;
  border-radius: 50%;
  margin: 15px auto;
  animation: animloader 1s linear infinite alternate;
  display: block;
  position: relative;
}

@keyframes animloader {
  0% {
    box-shadow: -38px -12px, -14px 0, 14px 0, 38px 0;
  }

  33% {
    box-shadow: -38px 0, -14px -12px, 14px 0, 38px 0;
  }

  66% {
    box-shadow: -38px 0, -14px 0, 14px -12px, 38px 0;
  }

  100% {
    box-shadow: -38px 0, -14px 0, 14px 0, 38px -12px;
  }
}

